import { TextField } from "@mui/material";

import Part from "./Part";

function Body() {
  return (
    <div className="body">
      <div className="priceInput">
        <p>Precio:</p>
        <TextField id="outlined-basic" label="$" variant="outlined"/>
      </div>
      <div className="options">
        <Part name='CPU' />
        <Part name='Placa Madre' />
        <Part name='RAM' />
        <Part name='GPU' />
        <Part name='Disco duro' />
        <Part name='Gabinete' />
        <Part name='Fuente de poder' />
      </div>
    </div>
  );
}

export default Body;