import { useState } from "react";

import { Box, Checkbox, FormControl, TextField, Typography, Slider, Select, Skeleton, MenuItem } from "@mui/material";

import { NumericFormat } from "react-number-format";

function Part({name}){

  const MIN = 0;
  const MAX = 100;

  const [value, setValue] = useState(MIN);
  const [partName, setPartName] = useState('Ten');

  const handleBlur = () => {
    if (value < MIN) {
      setValue(MIN);
    } 
    else if (value > MAX) {
      setValue(MAX);
    }
  }

  return(
    <Box sx={{ 
      display: 'flex',
      flexDirection: 'row',
      gap: '1vw',
      alignItems: 'center',
      minWidth: '98vw',
      padding: '2vh 0' 

    }}>
      <Box>
        <Checkbox defaultChecked />
      </Box>
      <Box 
        id="price"
        sx = {{
          width: 120
        }}
      >

        <Typography id="input-slider" gutterBottom>
          { name }
        </Typography>
        <Slider
          sx={{
            width: 120
          }}
          value={typeof value === 'number' ? value : MIN}
          step={1}
          min={MIN}
          max={MAX}
          onChange={(event, newValue) => setValue(newValue)}
        />
      </Box>
      <Box id="percent"
        sx={{
          display: 'flex',
          flexDirection: 'row',
          alignItems: 'center',
          gap: '5px'
        }}
      >
        <NumericFormat
          sx={{
            width: 60
          }}
          value={value}
          isAllowed={(values) => {
            if (!values.value) return true;
            const { floatValue } = values;
            return floatValue <= 100;
          }}
          customInput={TextField}
          size="small"
          onBlur={handleBlur}
          onChange={(event) => setValue(event.target.value === '' ? 0 : Number(event.target.value))}
        />
        <p>%</p>
      </Box>
      <Box>
        <NumericFormat 
          sx={{
            width: 150
          }} 
          value={12323} 
          customInput={TextField} 
          thousandSeparator
          prefix="$"
        />
      </Box>
      <Box id="partSelector">
        <FormControl size="small" sx={{ m: 1, width: 250 }}>
          <Select
            displayEmpty
            value={partName}
            onChange = {(event) => setPartName(event.target.value)}
          >
            <MenuItem value={10}>AMD Ryzen 7 3700x</MenuItem>
            <MenuItem value={20}>Intel Core i5-12400F</MenuItem>
            <MenuItem value={30}>MSI GeForce RTX 4060</MenuItem>
          </Select>
        </FormControl>

      </Box>
      <Box id="partPreview" sx={{ 
        display: 'flex', 
        flexDirection: 'row',
        gap: '2vw'
      }}>
        <Skeleton variant="rounded" width={120} height={120} />
        <Skeleton variant="rounded" width={120} height={120} />
        <Skeleton variant="rounded" width={120} height={120} />
        <Skeleton variant="rounded" width={120} height={120} />
      </Box>
    </Box>
  );
}

export default Part;