import { Box, FormControlLabel, Radio, RadioGroup } from '@mui/material';

import { useColorScheme } from '@mui/material/styles'

function Header() {

  const { mode, setMode } = useColorScheme();
  if (!mode) {
    return 'light';
  }

  return(
    <Box sx={{
      display: 'flex',
      width: '100%',
      justifyContent: 'space-between',
    }}>
      <p>PcPartPicker</p>
      <RadioGroup
        row
        name="tema"
        value = {mode}
        onChange={(event) => setMode(event.target.value)}
      >
        <FormControlLabel value="light" control={<Radio />} label="Claro"></FormControlLabel>
        <FormControlLabel value="dark" control={<Radio />} label="Oscuro"></FormControlLabel>
      </RadioGroup>
    </Box>
  );
}

export default Header;